.cards {
  display:flex;
  justify-content: space-between;
  line-height: 25px;
  font-size: 16px;
  color: #0f1726;
}

.cards__c {
  background: #fff;
  margin: 12px;
  min-height: 100px;
  text-align: center;
  box-sizing: border-box;
  box-sizing: border-box;
  padding: 30px 20px;
}

.cards__c_3 {
  flex-basis: 33%;
}

.cards__c_2 {
  flex-basis: 50%;
}

.cards .cards__c:first-child {
  margin-left: 0;
}

.cards .cards__c:last-child {
  margin-right: 0;
}

.card__header {
  border-bottom: 1px solid rgba(151,151,151, 0.36);
  padding-bottom: 40px;
  margin: 0 20px;
}

.card__header .btn {
  width: calc(100% - 80px);
  margin: 0 40px;
}

.card__body {
  text-align: left;
  box-sizing: border-box;
  padding: 0 60px;
}

.card__body ul {
  color: rgba(15, 23, 38, 0.7);
  padding: 0;
  list-style: none;
}

.card__body ul li {
  margin: 12px 0;
  display: flex;
}

.card__body ul li:before {
  content: '';
  width: 19px;
  height: 20px;
  min-width: 19px;
  min-height: 20px;
  background: url(../../assets/img/list.png);
  margin-right: 16px;
  background-size: cover;
}